header {
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  align-items: flex-start;
}

header nav span {
  color: #333;
  font-size: 1em;
  font-weight: 700;
  cursor: pointer;
}

header nav {
  padding: 0 2em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 3px 5px;
  transition: all 0.2s ease-in-out;
  z-index: 1;
}

header nav span {
  padding: 1em 0;
}
header nav:hover {
  background-color: #000;
}
header nav:hover span {
  color: #fff;
}
header nav:hover ul li a {
  color: #fff;
}

.menu {
  display: none;
  width: 24px;
  height: 24px;
}

header nav ul {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding: 1em 0;
}

header nav ul li {
  padding: 0 1em;
}

header nav ul li a {
  text-decoration: none;
  color: #333;
  font-size: 1em;
  font-weight: 700;
  position: relative;
}

header nav ul li a::before {
  content: "";
  position: absolute;
  width: 0%;
  height: 2px;
  bottom: -3px;
  left: 0;
  background-color: #fff;
  transition: all 200ms ease-in-out;
}

header nav ul li a:hover::before {
  width: 100%;
}

.mobileMenu {
  display: none;
}

@media only screen and (max-width: 600px) {
  header nav ul {
    display: none;
  }

  header nav {
    z-index: initial;
  }

  header nav:hover {
    background-color: initial;
  }

  header nav:hover span {
    color: initial;
  }
  header nav:hover ul li a {
    color: initial;
  }

  .menu {
    display: flex;
    width: 24px;
    height: 24px;
    cursor: pointer;
    z-index: 3;
  }

  .mobileMenu {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #ecc5c5;
    margin-left: -100%;
    transition: all 0.5s cubic-bezier(0.12, 0.54, 0.77, 1.21);
  }

  .mobileNav {
    margin-left: 0%;
    transition: all 0.5s cubic-bezier(0.12, 0.54, 0.77, 1.21);
  }
  .mobileMenu li {
    list-style-type: none;
    margin: 1em 0;
  }
  .mobileMenu li a {
    text-decoration: none;
    text-decoration: none;
    color: #333;
    font-size: 1em;
    font-weight: 700;
    position: relative;
  }
  .mobileMenu li a::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 0%;
    height: 2px;
    background-color: #333;
    transition: all 200ms ease-in-out;
  }
  .mobileMenu li a:hover::before {
    width: 100%;
  }
}
